import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

function Detail(props){

	let { id } = useParams();	//:id 자리에 사용자가 입력한 값 받아오기
	let history = useHistory();// 방문기록
	let 찾은물건 = props.shoes.find( e => {return e.id == id})//find 함수 사용
	let [알림창, 알림창변경] = useState(true);

	let 박스 = styled.div ` padding : 20px; `;// css를 미리 입힌 컴포넌트=> 쓰는건 선택사항
	let 제목  = styled.h4 ` font-size : 25px; color : ${ props => props.색상 }`;
	
	// 최신 라이프사이클 훅-> 컴포넌트 실행시 같이 실행됨
	useEffect( () => {

		// axios.get()

		// 2초 후에 코드를 실행, 여러 시간대로 중복 사용 가능
		let 타이머 = setTimeout( () => { 알림창변경(false) }, 1000);
		return ( () => {clearTimeout()} ) //timeout 오류 방지(일찍 사이트 나갔을때)
	}, []) //[]로 쓰면 업데이트 때 실행안됨, [알림창] 이런식으로도 씀

		return (
		<div className="container">
			
			{/* 제목 */}
			<박스>
				<제목 className = "m-5 red">Detail</제목>
			</박스>


			{/* 알림창 */}
			{
				(props.재고[찾은물건.id] < 5 && 알림창 === true)
				? <MyAlert/>
				: null
			}

			{/* 상품설명 및 주문, 뒤로가기 버튼 */}
			<div className="row">
				<div className="col-md-6">
				<img src= { "https://codingapple1.github.io/shop/shoes" + (parseInt(id)+1) + ".jpg" } width="100%" />
				</div>
				<div className="col-md-6 mt-4">
				<h4 className="pt-5">{찾은물건.title}</h4>
				<p>{찾은물건.content}</p>
				<p>{찾은물건.price}</p>
				<Info 찾은물건 = {찾은물건} 재고 = {props.재고}></Info>
				<button className="btn btn-danger" onClick = {() => {
					let temp = [...props.재고]
					
					temp[찾은물건.id] > 0 
					? temp[찾은물건.id] -= 1 : null
					props.재고변경(temp)
				}}>주문하기</button> 
				<button className="btn btn-danger" onClick = {() => {
					// 뒤로가는 함수, = history.push('/)
					history.goBack()
				}}>뒤로가기</button> 
				</div>
			</div>

		</div> 
	)
}

function Info(props) {
	return(
		<p>재고 : {props.재고[props.찾은물건.id]}</p>
	)
}

function MyAlert(){
	return(
		<div className = "my-alert2">
			<p>재고가 얼마 남지 않았습니다</p>
		</div>
	)
	
}



